import { encodePath } from '@nextcloud/paths';
import { generateUrl } from '@nextcloud/router';
import { getToken, isPublic } from '../utils/davUtils.js';

// preview width generation
const previewWidth = 256;
export default {
  name: 'TemplatePreview',
  inheritAttrs: false,
  props: {
    basename: {
      type: String,
      required: true
    },
    checked: {
      type: Boolean,
      default: false
    },
    fileid: {
      type: [String, Number],
      required: true
    },
    filename: {
      type: String,
      required: true
    },
    previewUrl: {
      type: String,
      default: null
    },
    hasPreview: {
      type: Boolean,
      default: true
    },
    mime: {
      type: String,
      required: true
    },
    ratio: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      failedPreview: false
    };
  },
  computed: {
    /**
     * Strip away extension from name
     *
     * @return {string}
     */
    nameWithoutExt() {
      return this.basename.indexOf('.') > -1 ? this.basename.split('.').slice(0, -1).join('.') : this.basename;
    },
    id() {
      return "template-picker-".concat(this.fileid);
    },
    realPreviewUrl() {
      // If original preview failed, fallback to mime icon
      if (this.failedPreview && this.mimeIcon) {
        return this.mimeIcon;
      }
      if (this.previewUrl) {
        return this.previewUrl;
      }
      // TODO: find a nicer standard way of doing this?
      if (isPublic()) {
        return generateUrl("/apps/files_sharing/publicpreview/".concat(getToken(), "?fileId=").concat(this.fileid, "&file=").concat(encodePath(this.filename), "&x=").concat(previewWidth, "&y=").concat(previewWidth, "&a=1"));
      }
      return generateUrl("/core/preview?fileId=".concat(this.fileid, "&x=").concat(previewWidth, "&y=").concat(previewWidth, "&a=1"));
    },
    mimeIcon() {
      return OC.MimeType.getIconUrl(this.mime);
    }
  },
  methods: {
    onCheck() {
      this.$emit('check', this.fileid);
    },
    onFailure() {
      this.failedPreview = true;
    }
  }
};